@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,100..1000&display=swap');
$pFont: "Roboto Flex", sans-serif;
$mFont: 'Bebas Neue', monospace;
$backgroundColor: #1B1B1B;
$c1: #9fef00;
$desktop: 1440px;

// .sblSecHome {background: red;}
.sblSecAbout {background: #1B1B1B; z-index: 2;}
.sblSecPkgs {background: #1B1B1B; z-index: 3;}
.sblSecTeam {background: #1B1B1B; z-index: 4;}
.sblSecContact {background: #1B1B1B; z-index: 5;}

.loader-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    background-color: #1B1B1B;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
}
.loadLogo {
    width: 40%;
    opacity: 0;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: $mFont;
}

body {
    margin: auto;
    padding: 0;
    background: #1B1B1B;
    font-weight: 400;
    max-width: 1440px;
    color: #f8f8f8;
}
button {
    background: transparent;
    border: 0;
}
a {
    text-decoration: none;
    color: #f8f8f8;
    cursor: pointer;
    font-size: calc((22px / $desktop) * 100vw);
}
p {
    font-family: $pFont;
    font-size: calc((17px / $desktop) * 100vw);
    line-height: 160%;
}
i, span {
    color: #9fef00;
}
h1 {
    font-size: calc((100px / $desktop) * 100vw);
    line-height: 100%;
}
h3 {
    font-size: calc((24px / $desktop) * 100vw);
}
ul {
    list-style: none;
}
.sblMain {
    width: 100%;
    height: 100vh;
    display: flex;
    overflow-x: hidden;
    overflow-y: hidden;
}

.sblSecHome, .sblSecAbout, .sblSecPkgs, .sblSecTeam, .sblSecContact{
    width: 100%;
    height: 100%;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.sblSecAbout {
    margin-left: -16%;
}
.sblSecPkgs {
    margin-left: -96%;
}
.sblSecTeam {
    margin-left: -96%;
}
.sblSecContact {
    margin-left: -96%;
}

.tShadow {
    width: 100%;
    position: relative;
      &:before {
        content: 'Wild Currents';
        position: absolute;
        top: 32px; //responsive
        left: 8px; //responsive
        color: #000;
        width: 100%;
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: rgb(0, 0, 0);
        -webkit-text-fill-color: #000;
        z-index: -1;
      }
}
.tStroke {
    color: transparent !important; /* Atur warna teks transparan */
    -webkit-text-stroke-width: .8px; /* Atur lebar outline */
    -webkit-text-stroke-color: #f8f8f8; /* Atur warna outline */
    -webkit-text-fill-color: transparent;
    stroke-width: .8px;
}
.actBtn {
    background: rgba(0, 0, 0, 0);
    border: 1px solid #f8f8f8;
    padding: calc((10px / $desktop) * 100vw) calc((20px / $desktop) * 100vw); //responsive
    position: relative;
    cursor: pointer;
    transition: .3s;
    a {
      width: 100%;
      height: 100%;
      font-size: calc((24px / $desktop) * 100vw);
      &:before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: calc(100% + 3px);
        border-right: 1px solid #f8f8f8;
        border-bottom: 1px solid #f8f8f8;
        display: block;
        transform: translate(calc((6px / $desktop) * 100vw), calc((6px / $desktop) * 100vw)); //responsive
        z-index: -1;
        transition: .5s;
      }
    }
      &:hover {
        background: rgba(159, 239, 0, 1);
        border: 1px solid #9fef00;
        a {
            color: #1B1B1B;
          &:before {
            transform: translate(0px, 0px);
            height: 100%;
            border-right: 1px solid transparent;
            border-bottom: 1px solid transparent;
          }
        }
      }
  }

/* NAVIGATION MAIN HERE */
  .navMain {
    height: 100%;
    background: #9fef00;
    display: flex;
    justify-content: flex-end;
    .navCont {
        width: 100%;
    }
    img {
        width: calc((30px / $desktop) * 100vw); //responsive
    }
    a {
        height: 100%;
        background-color: #9fef00;
        color: #1B1B1B;
        border-left: 1px solid #f8f8f8;
        border-top: 1px solid #f8f8f8;
        border-bottom: 1px solid #f8f8f8;
        padding: 20px 10px; //responsive
        margin: 0;
        display: flex;
        flex-direction: column;
        transition: .3s;
        z-index: 1;
        &:hover {
            background-color: #f8f8f8;
        }
    }
    .pages, .ico {
        height: 100%;
    }
    .ico {
        text-align: center;
    }
    .pages {
        text-orientation: sideways;
        transform: scale(-1);
        writing-mode: vertical-rl;
        display: flex;
        align-items: center;
    }
    a:nth-child(0) {
        border-right: 1px solid #f8f8f8;
    }
    i {
        color: #1B1B1B;
    }
}
/* NAVIGATION MAIN HERE */

.navBtn {
    width: 100%;
    height: 6%;
    display: flex;
    a {
        font-size: calc((22px / $desktop) * 100vw); //responsive
        cursor: pointer;
    }
}
.navPrev, .navNext {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
}
.navNext {
    justify-content: flex-end;
}
/* NAVIGATION BUTTON END HERE */

/* MAIL BUTTON HERE */
.mail {
    display: flex;
    justify-content: flex-end;
    font-size: calc((22px / $desktop) * 100vw);
}
.mailBox {
    display: flex;
    border: 1px solid #f8f8f8;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    &:hover {
        .mailBoxIco {
            background: #9fef00;
        }
        i {
            color: #1B1B1B;
        }
    }
    div {
        padding: calc((10px / $desktop) * 100vw) calc((20px / $desktop) * 100vw);
    }
    .mailBoxTxt {
        border-left: 1px solid #f8f8f8;
        span {
            color: #f8f8f8;
        }
    }
    .mailBoxIco {
        position: relative;
        transition: .3s;
    }
}
/* MAIL BUTTON HERE */

/* SECTION HOME STYLES START */
.homeMain {
    width: 82%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 4%;
}
.homeMainTop {
    height: 15%;
    width: 100%;
    display: flex;
    align-items: center;
    img {
        object-fit: cover;
        width: 50%;
    }
    .mail, .logo {
        width: 50%;
    }
}

.homeMainMid {
    height: 79%;
    display: flex;
    position: relative;
    img {
        width: 78%;
        object-fit: cover;
        border-radius: 10%;
    }
    .heroImg {
        text-align: right;
    }
    .heroTxt {
        position: absolute;
        bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        row-gap: calc((15px / $desktop) * 100vw);
        h1 {
            color: #f8f8f8;
        }
    }
}

.homeSpace {
    width: 18%;
}
/* SECTION HOME STYLES ENDS */

/* SECTION ABOUT STYLES START */
.navAbout {
    width: 4%;
}

.aboutMain {
    width: 82.5%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 4%;
}
.aboutMainHero {
    width: 100%;
    height: 94%;
    display: flex;
    flex-direction: column;
    position: relative;
}
.txtHero {
    width: 100%;
    height: 60%;
    h1 {
        font-size: calc((125px / $desktop) * 100vw);
    }
}
.txtHero2 {
    width: 100%;
    height: 40%;
    display: flex;
    position: relative;
    column-gap: calc((30px / $desktop) * 100vw);
    img {
        width: 100%;
        object-fit: cover;
    }
    .txtImg {
        width: 35%;
    }
    .txtPar {
        width: 65%;
        padding-right: 24%;
    }
    .sblVid {
        position: absolute;
        width: 26%;
        bottom: 0;
        right: -50px;
        video {
            width: 100%;
            object-fit: cover;
        }
    }
}

.aboutSpace {
    width: 13.5%;
}
/* SECTION ABOUT STYLES ENDS */

/* SECTION PACKAGES STYLES START */
.navPkgs {
    width: 4%;
}
.pkgsMain {
    width: 88%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .navBtn {
        padding: 0 4%;
    }
}
.pkgsMainHero {
    height: 94%;
}
.pkgsSpace {
    width: 8%;
}
.pkgsT {
    height: 30%;
    padding: 1% 4%;
    p {
        width: 60%;
    }
}
.pkgsM {
    width: 100%;
    height: 70%;
    cursor: grab;
    h2 {
        font-size: calc((60px / $desktop) * 100vw);
    }
    span, h3 {
        font-size: calc((32px / $desktop) * 100vw);
    }
}
.pkgsSwipe {
    width: 100%;
}
.pkgsSwiping {
    width: 75% !important;
    height: 100%;
    position: relative;
    display: flex !important;
    flex-direction: column;
    row-gap: calc((10px / $desktop) * 100vw);
    justify-content: flex-end;
    align-items: flex-start;
    padding: 1% 2%;
    img {
        width: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        z-index: -1;
        filter: brightness(50%) contrast(100%);
    }
    p {
        width: 60%;
        padding-bottom: calc((20px / $desktop) * 100vw);
    }
}
.packIco {
    display: flex;
    align-items: center;
    column-gap: calc((40px / $desktop) * 100vw);
    div {
        display: flex;
        align-items: center;
        column-gap: calc((20px / $desktop) * 100vw);
    }
    i {
        font-size: calc((30px / $desktop) * 100vw);
    }
}
/* SECTION PACKAGES STYLES ENDS */

/* SECTION TEAM STYLES START */
.navTeam {
    width: 4%;
}
.teamMain {
    width: 92%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 4%;
}
.teamMainHero {
    height: 100%;
    width: 100%;
    padding: 1% 0;
}
.teamT {
    height: 30%;
}
.teamM {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 70%;
    column-gap: 4%;
    img {
        width: 100%;
        object-fit: cover;
        filter: grayscale(100%);
        transition: .3s;
    }
}
.lines {
    background-color: #f8f8f8;
    width: 5%; //responsive
    height: 100%;
} .l1 {
    opacity: .4;
} .l2  {
    opacity: .6;
} .l3 {
    opacity: .8;
}
.teamSecImg{
    width: 38%;
    height: 100%;
    position: relative;
    overflow: hidden !important;
    cursor: grab;
    &:hover {
        img {
            filter: grayscale(0%) !important;
        }
    }
}
.teamSecName {
    width: 22%;
    height: 100%;
    h2 {
        font-size: 45px;
    }
}
.teamSpace {
    width: 4%;
}
/* SECTION TEAM STYLES ENDS */

/* SECTION CONTACT STYLES START */
.navContact {
    width: 4%;
}
.contactMain {
    width: 96%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 4%;
    position: relative;
}
.contactMainHero {
    height: 94%;
    padding: 1% 0;
}
.contactT {
    height: 50%;
    border-bottom: 5px solid #f8f8f8;
    display: flex;
    flex-direction: column;
    row-gap: 6%;
    position: relative;
}
.contMainT {
    display: flex;
    align-items: center;
    column-gap: 2%;
    font-size: calc((24px / $desktop) * 100vw) !important;
}
.contSoc {
    display: flex;
    align-items: center;
    column-gap: 2%;
    i {
        font-size: calc((42px / $desktop) * 100vw);
    }
}
.own {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 50%;
}
.contactB {
    height: 50%;
    display: flex;
    flex-direction: column;
    row-gap: 6%;
    h1 {
        padding-top: 2%;
    }
    h2 {
        color: #9fef00;
    }
}
.jangga {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 26%;
}
/* SECTION CONTACT STYLES ENDS */

/* ADDRESS HAS BEEN DISABLE WAITING FOR MSG */
.address {
    display: none;
}
/* ADDRESS HAS BEEN DISABLE WAITING FOR MSG */